.container {
  max-width: 1320px;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;
  outline: 1px solid brown;
}

.header {
  padding-top: 23px;
  padding-bottom: 29px;
}
.header__top {
  display: flex;
}

.catalog-menu__item:not(:last-child) {
  margin-bottom: 26px;
}
.catalog-menu__btn-secondary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 400px;
  color: #0a0a0a;
}

.dropdown {
  display: none;
}
.dropdown__link {
  color: var(--text);
}

.logo {
  margin-right: 23px;
}

.btn-main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0 8px;
  padding: 14px 19px 12px 22px;
  border-radius: var(--border-radius);
  width: 132px;
  height: 48px;
  background-color: var(--red);
  font-size: 16px;
  color: var(--white);
}
.btn-main__line-box {
  position: relative;
  display: inline-block;
  width: 18px;
  height: 12px;
  background: transparent;
  z-index: 2;
}
.btn-main__line {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: var(--border-radius);
  background: var(--white);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.btn-main__line:nth-child(1) {
  top: 0;
}
.btn-main__line:nth-child(2) {
  top: 6px;
}
.btn-main__line:nth-child(3) {
  top: 6px;
}
.btn-main__line:nth-child(4) {
  top: 11px;
}
.btn-main-open .btn-main__line:nth-child(1) {
  transform: scaleX(0);
}
.btn-main-open .btn-main__line:nth-child(2) {
  transform: rotate(45deg);
}
.btn-main-open .btn-main__line:nth-child(3) {
  transform: rotate(-45deg);
}
.btn-main-open .btn-main__line:nth-child(4) {
  transform: scaleX(0);
}